import { createDoc, fetchRecentDocs } from "../api/api";
export default {
  name: "history",
  data: function data() {
    return {
      webTitle: '最近浏览',
      chart: "",
      folderDialog: false,
      docList: []
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      fetchRecentDocs(1).then(function (res) {
        if (res.status === 200) {
          if (res.data.length === 0) {
            _this.docList = [];
          } else {
            res.data.forEach(function (i) {
              _this.docList = [];

              _this.docList.push({
                docId: i.document.id,
                docName: i.document.name,
                readTime: i.read_time
              });
            });
          }
        }
      }).catch(function (e) {
        _this.$message({
          message: e,
          type: 'error'
        });
      });

      if (localStorage.getItem('chart')) {
        this.chart = localStorage.getItem('chart');
      } else {
        this.chart = '图标';
      }
    },
    toNewDoc: function toNewDoc() {
      var _this2 = this;

      createDoc(0).then(function (res) {
        if (res.status === 201) {
          _this2.$message({
            message: '新建文档成功',
            type: 'info'
          });

          _this2.$router.push({
            name: 'editorPage',
            params: {
              docId: res.data.id
            }
          });
        }
      }).catch(function (e) {
        if (e.response.status === 401) {
          _this2.$message({
            message: '您没有权限',
            type: 'error'
          });
        }
      });
    },
    changeChart: function changeChart(value) {
      this.chart = value;
      localStorage.setItem('chart', value);
    },
    changeVisible: function changeVisible(val) {
      this.folderDialog = val;
    },
    toTemplate: function toTemplate() {
      this.$router.push({
        name: 'templates'
      });
    }
  }
};